.embeds {
}

.embed-wrapper {
	position: relative;
	background: red;
	iframe {
		position: absolute;
		top: 0px;
		right: 0px;
		bottom: 0px;
		left: 0px;
		width: 100%;
		height: 100%;
		border: none;
	}
	&._video {
		padding-top: 56.25%;
		p {
			margin-bottom: 0;
		}
	}
	&._chat {
		min-height: rem(336);
	}
}

.embed-group {
	@include bp-1024 {
		display: flex;
	}
	&._video_and_chat {
		@include bp-1024 {
			.embed-group__item._video {
				flex: 2;
			}
			.embed-wrapper._chat {
				background: lightblue;
				width: rem(336);
			}
		}
	}
}
